import firebase from 'firebase';
import '../ControlPanelStyle.css';
import maildata from '../maildata.json';
import Comps from '../companies.json';

var check;
var AddData;
var Auid;
var Duid;
var Uuid;
var AddTableData;
var DelTableData;
var UpdTableData;
var ComDep;

function GetComp(cd) {
  Comps.map(function (company) {
    if(company.company === cd){
      ComDep = (company.companyname);
    }
    return ComDep;
  });
}

export function StaffTableActionAdd(name, surname, role, company, email, days, workcompany) {
  check = 0;
  var date = new Date();
  var reg_date = date.getMonth() + " " + date.getDate() + " " + date.getFullYear();
  let userid = () => {
    return Math.floor((1 + Math.random()) * 0x100000).toString(16);
  }
  var temppass = userid();
  var boss;
  var loginname = (name.charAt(0).toLowerCase()) + "." + (surname.toLowerCase());
  if((name !== undefined)&&(surname !== undefined)&&(role !== undefined)&&(company !== undefined)&&(email !== undefined)&&(days !== undefined)&&(workcompany !== undefined)){
    return (
      AddTableData = firebase.database().ref("Staff"),
      AddTableData.once('value', (snapshot) => {
        snapshot.forEach((childSnapshot) => {
          AddData = childSnapshot.val();
          Auid = AddData.id;
          for(var i = 0; i <= AddData.workCompany.length; i = i + 6){
            if(((AddData.workCompany.slice(i, i + 2) === company.slice(0, 2))&&(AddData.workCompany.slice(i + 2, i + 4) === "00"))||((AddData.workCompany.slice(i, i + 4) === company))){
              if((role === "Generalinis direktorius")||(role === "Generalinė direktorė")){boss = name + " " + surname}
              if(((role === "Padalinio vadovas")||(role === "Padalinio vadovė"))&&((AddData.role === "Generalinis direktorius")||(AddData.role === "Generalinė direktorė"))){boss = AddData.name + " " + AddData.surname}
              if(((role === "Skyriaus vadovas")||(role === "Skyriaus vadovė"))&&((AddData.role === "Padalinio vadovas")||(AddData.role === "Padalinio vadovė"))){boss = AddData.name + " " + AddData.surname}
              if(((role === "Buhalteris")||(role === "Buhalterė"))&&((AddData.role === "Skyriaus vadovas")||(AddData.role === "Skyriaus vadovė"))){
                boss = AddData.name + " " + AddData.surname;
              }
            }
          }
          if(AddData.staffid === loginname){
            check = 1;
          }
        });
        if(check === 0){
          var addUser = firebase.database().ref("Staff");
          var addNewUser = addUser.push();
          addNewUser.set({
              id: (Auid + 1),
              staffid: loginname,
              name: name,
              surname: surname,
              role: role,
              email: email,
              company: company,
              password: temppass,
              days: days,
              regDate: reg_date,
              workCompany: workcompany,
              boss: boss
          })
          GetComp(company);
          window.Email.send({
            Host: "smtp.gmail.com",
            Username: maildata.emailname,
            Password: maildata.emailpassword,
            To: email,
            From: maildata.emailname,
            FromName: ComDep,
            Subject: "Registracija",
            Body: "Sveiki <b>" + name + " " + surname + '</b>,<br/><br/>jūs buvote pridėtas prie sistemos.<br/>Prisijungti galite <a href="https://linas-semiotas.github.io/react-project/">čia</a>, jūsų prisijungimo duomenys yra:<br/><br/><b>Prisijungimo vardas: ' + loginname + "<br/>Slaptažodis: "+ temppass + "</b>"
          })
          alert("Vartotojas sėkmingai pridėtas");
        }
      })
    );
  }
  else{
    alert("Yra tuščių laukelių!");
  }
}

export function StaffTableActionDel(delindex) {
  return (
    DelTableData = firebase.database().ref("Staff").orderByChild("id").equalTo(delindex),
    DelTableData.once('value', (snapshot) => {
        snapshot.forEach((childSnapshot) => {
          Duid = childSnapshot.key;  
        });
        var delUser = firebase.database().ref("Staff/" + Duid);
        delUser.remove(); 
    })
  );
}
export function StaffTableActionUpd(updindex, staffid, name, surname, role, company, email, days, workcompany) {
  if((updindex !== undefined)&&(staffid !== undefined)&&(name !== undefined)&&(surname !== undefined)&&(role !== undefined)&&(company !== undefined)&&(email !== undefined)&&(days !== undefined)&&(workcompany !== undefined)){
    return (
      UpdTableData = firebase.database().ref("Staff").orderByChild("id").equalTo(updindex),
      UpdTableData.once('value', (snapshot) => {
          snapshot.forEach((childSnapshot) => {
            Uuid = childSnapshot.key;
          });
          var updUser = firebase.database().ref("Staff/" + Uuid);
          updUser.update({
            staffid: staffid,
            name: name,
            surname: surname,
            role: role,
            email: email,
            company: company,
            days: days,
            workCompany: workcompany
          }); 
      })
    );
  }
  else{
    alert("Yra tuščių laukelių!");
  }
}